<template>
  <div v-if="userAvatar">
    <v-avatar class="justify-center icon" :size="size">
      <v-img :src="userAvatar" />
    </v-avatar>
  </div>
</template>

<script lang="js">
import Vue from "vue";
import { mapGetters } from "vuex";

export default Vue.extend( {
  name: "UserIcon",
  props: {
    url: String,
    size: {
      type: String,
      default: "48",
    },
  },
  computed: {
    ...mapGetters(["user"]),
    userAvatar() {
      return this.url ? this.url : this.user.avatar_url;
    },
  }
});
</script>

<style scoped>
.icon {
  width: 100%;
  height: 100%;
}
</style>
